.accepted--container {
  width: 100%;
  height: 100vh;
  background: #f4c4f3; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to top,
    #fc67fa,
    #f4c4f3
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #fc67fa,
    #f4c4f3
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  display: flex;
  flex-direction: column;
  // justify-content: space-between;
  .row--container {
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 50%;
    .pic--container {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50%;
      .pic--border {
        background: linear-gradient(to right, red, purple);
        width: 415px;
        height: 340px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
        animation: grow2 1s infinite;
        .bg-pics {
          width: 400px;
          height: 325px;
          border-radius: 10px;
        }
      }
    }
  }
  .hero--container {
    position: absolute;
    height: 100vh;
    width: 100vw;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;
    .hero--border {
      background: linear-gradient(to right, red, purple);
      width: 415px;
      height: 515px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 10px;
      animation: grow 2s infinite;
      .hero--image {
        height: 500px;
        width: 400px;
        border-radius: 10px;
      }
    }
  }
}

.hero--text{
  position: absolute;
  top: 50%;
  left: 10%;
  z-index: 5;
  font-size: 40px;
  color: #ff0000;
  font-weight: 400;
  -webkit-text-stroke: 2px black;
  font-family: "Protest Riot", sans-serif;
}

@keyframes grow {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes grow2 {
  0% {
    transform: rotate(0deg);
  }
  20% {
    transform: rotate(5deg);
  }
  40% {
    transform: rotate(-5deg);
  }
  60% {
    transform: rotate(-5deg);
  }
  80% {
    transform: rotate(5deg);
  }
}

#background-wrap {
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 10;
  width: 100vw;
  height: 100vh;
}

/* KEYFRAMES */

@-webkit-keyframes animateBubble {
  0% {
    margin-top: 1000px;
  }
  100% {
    margin-top: -100%;
  }
}

@-moz-keyframes animateBubble {
  0% {
    margin-top: 1000px;
  }
  100% {
    margin-top: -100%;
  }
}

@keyframes animateBubble {
  0% {
    margin-top: 1000px;
  }
  100% {
    margin-top: -100%;
  }
}

@-webkit-keyframes sideWays {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: 50px;
  }
}

@-moz-keyframes sideWays {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: 50px;
  }
}

@keyframes sideWays {
  0% {
    margin-left: 0px;
  }
  100% {
    margin-left: 50px;
  }
}

